import {rasterTrafficIncidentTilesLayer} from '../../endpoints';
import rasterStylesFactory from './rasterStylesFactory';

const LAYER_NAMES = {
    S1: 'tomtomRasterTrafficIncidentsS1',
    S2: 'tomtomRasterTrafficIncidentsS2',
    S3: 'tomtomRasterTrafficIncidentsS3',
    NIGHT: 'tomtomRasterTrafficIncidentsNight'
};

const S1 =
    rasterStylesFactory('raster-traffic-incidents', LAYER_NAMES.S1, rasterTrafficIncidentTilesLayer, {
        '{style}': 's1',
        '{tileSize}': 512
    });
const S2 =
    rasterStylesFactory('raster-traffic-incidents', LAYER_NAMES.S2, rasterTrafficIncidentTilesLayer, {
        '{style}': 's2',
        '{tileSize}': 512
    });
const S3 = rasterStylesFactory('raster-traffic-incidents', LAYER_NAMES.S3, rasterTrafficIncidentTilesLayer, {
    '{style}': 's3',
    '{tileSize}': 512
});
const NIGHT = rasterStylesFactory('raster-traffic-incidents', LAYER_NAMES.NIGHT, rasterTrafficIncidentTilesLayer, {
    '{style}': 'night',
    '{tileSize}': 512
});

const STYLES = {
    S1,
    S2,
    S3,
    NIGHT
};

export default function(style) {
    return STYLES[style.toUpperCase().replace('-', '_')];
}



